<template>
    <main id="weather">
        <div style="margin-top:50px;padding-top:25px;padding-bottom:25px;">
            <img :src="wea_img" style="height:70px;">
            <div style="font-size:25px;">地区:{{cityZh}}</div>
            <div style="font-size:15px;padding-left:15px;padding-right:15px;" v-for="weather in weatherArray.slice(0,1)" :key="weather.id">
                Tip:{{weather.air_tips}}
            </div>
        </div>
        <collapse v-model="activeNames">
            <collapse-item title="今日指标" name="1" icon="orders-o">
                <div id="todayData" class="row" v-for="weather in weatherArray.slice(0,1)" :key="weather.id">
                    <div class='col' style="font-size:15px">
                        <span>
                            <b>天气:</b> {{weather.wea}}
                        </span><br />
                        <span>
                            <b>当前温度:</b> {{weather.tem}}
                        </span><br />
                        <span>
                            <b>风速:</b> {{weather.win_speed}}
                        </span><br />
                        <span>
                            <b>湿度:</b> {{weather.humidity}}
                        </span>
                    </div>
                    <div class='col' style="font-size:15px">
                        <span>
                            <b>白天温度:</b> {{weather.tem1}}
                        </span><br />
                        <span>
                            <b>夜晚温度:</b> {{weather.tem2}}
                        </span><br />
                        <span>
                            <b>空气质量:</b> {{weather.air_level}}
                        </span><br />
                        <span>
                            <b>空气指数:</b> {{weather.air}}
                        </span>
                    </div>
                </div>
            </collapse-item>
            <collapse-item title="今日时报" name="2" icon="underway-o">
                <div id="hourData" class="row">
                    <div class="col" v-for="hour in hoursArray" :key="hour.id" style="margin-bottom:25px;">
                        <span style="font-size:13px">
                            {{hour.day}}
                        </span><br />
                        <span style="font-size:18px">
                            {{hour.tem}}
                        </span><br />
                        <span style="font-size:16px">
                            {{hour.wea}}
                        </span><br />
                    </div>
                </div>
            </collapse-item>
            <collapse-item title="未来6日" name="3" icon='bar-chart-o'>
                <div id="sixDayData" class="row">
                    <div class="col" v-for="(weather) in weatherArray.slice(1, 7)" :key="weather.id"
                        style="margin-bottom:25px;">
                        <img :src="getImgUrl(weather.wea_img)" oncontextmenu="return false" onselectstart="return false"
                            ondragstart="return false" style="width:65px;" /><br />
                        <div style="font-size:12px;">{{weather.day}}</div>
                        <div style="font-size:15px;">{{weather.wea}}</div>
                    </div>
                </div>
            </collapse-item>
        </collapse>

        <!-- loading弹出层 -->
        <popup v-model="showLoading" style="height:150px;width:150px;padding:60px;">
            <loading type="spinner" color="#1989fa" />
        </popup>
    </main>
</template>

<script>
    import {
        Collapse,
        CollapseItem,
        Popup,
        Loading
    } from 'vant';
    export default {
        name: 'weather',
        data() {
            return {
                activeNames: ['1', '2', '3'],
                weather: {
                    day: "", //天,4日(今天)
                    date: "", //年月日,2019-08-04
                    week: "", //周几,星期日
                    wea: "", //天气,雷阵雨
                    wea_img: "", //图片名称,lei
                    air: "", //空气指数,55
                    humidity: "", //湿度,56
                    air_level: "", //空气等级,良
                    air_tips: "", //小贴士
                    tem1: "", //白天温度
                    tem2: "", //晚上温度
                    tem: "", //当前温度
                    win_speed: "" //风速,<3级
                },
                hoursArray: [],
                weatherArray: [],
                cityZh: '',
                wea_img: "",
                showLoading: false
            }
        },
        components: {
            Collapse,
            CollapseItem,
            Popup,
            Loading
        },
        methods: {
            getWeatherByCityId() {
                this.showLoading = true;
                this.axios({
                    method: "get",
                    url: "/mobile/weather/getWeather.action"
                }).then((response) => {
                    var weatherJson = response.data[0];
                    var hoursJson = response.data[1];
                    var cityJson = response.data[2];

                    this.weatherArray = JSON.parse(weatherJson);
                    this.hoursArray = JSON.parse(hoursJson);
                    this.cityZh = cityJson;
                    this.wea_img = require("../../assets/images/weather/" + this.weatherArray[0].wea_img +
                        ".png");
                    this.$router.push('/weather');

                    this.showLoading = false;
                }).catch((error) => {
                    console.log(error);
                });


            },
            getImgUrl(wea_img) {
                return require("../../assets/images/weather/" + wea_img + ".png");
            },
        },
        created() {
            this.getWeatherByCityId();
        }
    }
</script>

<style scoped>
    main {
        text-align: center;
        height: 1250px;
    }

    #todayData {
        background-color: bisque;
        padding-top: 15px;
        padding-bottom: 15px;
        margin: 0px;
    }

    #hourData {
        background-color: lemonchiffon;
        padding-top: 24px;
        margin: 0px;
    }

    #sixDayData {
        background-color: oldlace;
        padding-top: 25px;
        margin: 0px;
    }
</style>