<template>
    <div id="register">
        <cell-group>
            <field v-model="user.email" placeholder="请输入邮箱" label="邮箱" left-icon="envelop-o" required :error-message="emailError"/>
            <field v-model="user.name" placeholder="请输入姓名" label="姓名" left-icon="user-o"  :error-message="nameError"/>
            <field v-model="user.password" placeholder="请输入密码" type="password" label="密码" left-icon="bag-o" required :error-message="passwordError"/>
            <field v-model="data.confirmPassword" placeholder="请重复密码" type="password" label="重复密码" left-icon="bag-o" required  :error-message="confirmPasswordError"/>
            <br>
            <div style="font-size:18px;text-align:center;">证明你不是机器人</div>
            <slide-check v-on:finishCheck="$emit('finishCheck')"></slide-check>
            <br>
        </cell-group>
    </div>
</template>


<script>
    import {
        CellGroup,
        Field,
        Button
    } from 'vant';

    import slideCheck from '../../components/mobile/SliderCheck.vue';

    export default {
        name: 'login',
        data(){
            return{
                emailError:'',
                nameError: '',
                passwordError: '',
                confirmPasswordError: ''
            }
        },
        components: {
            CellGroup,
            Field,
            vButton: Button,
            slideCheck
        },
        props: ['user','data']
    }
</script>

