<template>
    <div id="app">
        <header>
            <nav-bar left-text="切换城市" :fixed="true" @click-right="getCity" @click-left="showPicker = true">
                <span slot="title" style="font-size:30px;">{{location}}</span>
                <icon name="location-o" slot="right" style="font-size:30px;"></icon>
            </nav-bar>
        </header>

        <!-- 下拉刷新 -->
        <pull-refresh v-model="isLoading" @refresh="onRefresh">
            <transition mode="out-in">
                <router-view ref="routerView" v-on:setLocation="setLocation"></router-view>
            </transition>
        </pull-refresh>

        <footer>
            <div
                style="background-color:rgb(133, 216, 255);color:black;font-size:15px;padding-bottom:50px;text-align:center;">
                Copyright ©2019 WuZhenghua</div>
            <tabbar route v-model="active">
                <tabbar-item icon="home-o" replace to="/home">首页</tabbar-item>
                <tabbar-item icon="flower-o" replace to="/weather">天气</tabbar-item>
                <tabbar-item icon="friends-o" replace to="/user">用户</tabbar-item>
                <tabbar-item icon="info-o" replace to="/about">关于</tabbar-item>
            </tabbar>
        </footer>

        <!--选择地区弹出层-->
        <popup v-model="showPicker" position="bottom">
            <picker :columns="columns" @change="onChange" show-toolbar title="切换城市" @cancel="showPicker = false"
                @confirm="getLocation" />
        </popup>

    </div>
</template>

<script>
    import "bootstrap/dist/css/bootstrap.css";
    import router from '../../routers/mobile/router.js';

    import {
        Tabbar,
        TabbarItem,
        NavBar,
        Icon,
        Button,
        Picker,
        Popup,
        PullRefresh
    } from 'vant';

    export default {
        name: 'app',
        components: {
            Tabbar,
            TabbarItem,
            NavBar,
            Icon,
            vButton: Button,
            Picker,
            Popup,
            PullRefresh
        },
        data() {
            return {
                active: 0,
                location: '定位中...',
                showPicker: false,
                provinceArrray: [],
                cityArray: [],
                isLoading: false
            }
        },
        methods: {
            onRefresh() {
                var _this = this;
                setTimeout(() => {
                    this.isLoading = false;

                    if (_this.$route.path == "/weather") {
                        _this.$refs.routerView.getWeatherByCityId();
                    }

                }, 800);
            },
            onChange(picker, values) {
                // picker.setColumnValues(1, citys[values[0]]);
                // console.log(picker.getColumnValue(1));
                this.getCityByProvince(picker.getColumnValue(0));
            },
            async getAllProvince() { //获取所有省份
                await this.axios({
                    method: "get",
                    url: "/mobile/city/getAllProvince.action"
                }).then((response) => {
                    this.provinceArrray = response.data;
                }).catch((error) => {
                    console.log(error);
                });
            },
            getCityByProvince(province) { //根据省份名称获取所有地区名称
                this.axios({
                    method: "get",
                    params: {
                        province
                    },
                    url: "/mobile/city/getCityByProvince.action"
                }).then((response) => {
                    this.cityArray = response.data;
                }).catch((error) => {
                    console.log(error);
                });
            },
            getLocation(values, index) { //显示顶部定位数据,随着所选地区改变,picker组件自带的确认方法,需要传入两个参数
                this.location = values[1];
                var provinceZh = values[0];
                this.setCityIdToCookie(this.location, provinceZh);
                this.showPicker = false;
            },
            setLocation(cityZh,provinceZh) { //设置顶部地区    ++++++++++++++++++++++++++++++++++++++++++++++
                this.location = cityZh;
                this.setCityIdToCookie(cityZh, provinceZh);
            },
            setCityIdToCookie(city, province) {
                this.axios({
                    method: "get",
                    url: "/mobile/city/setCityIdToCookie.action",
                    params: {
                        cityZh: city,
                        provinceZh: province
                    }
                }).then((response) => {
                    if (this.$route.path == "/weather") {
                        this.$refs.routerView.getWeatherByCityId();
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            removeCityIdFromCookie() {
                this.axios({
                    method: "get",
                    url: "/mobile/city/removeCityIdFromCookie.action"
                }).catch((error) => {
                    console.log(error);
                })
            },
            getCity() { //定位地区
                this.removeCityIdFromCookie(); //定位不需要取cookie里的城市id,所以先清除
                this.location = '定位中...';
                const geolocation = new BMap.Geolocation();
                geolocation.getCurrentPosition((position) => {
                    let city = position.address.city; //获取城市信息
                    let province = position.address.province; //获取省份信息
                    city = city.replace("市", "");
                    city = city.replace("区", "");
                    city = city.replace("县", "");
                    province = province.replace("省", "");
                    province = province.replace("市", "");
                    province = province.replace("特别行政区", "");

                    this.setCityIdToCookie(city, province);
                    this.location = city;
                    // $("#locationLoading").css("display","none");
                }, (error) => {
                    // $("#locationLoading").css("display","none");
                    this.location = "定位失败";
                    alert("请确定是否开启了定位,或者手动选择位置");
                }, {
                    provider: 'baidu'
                });
            }
        },
        mounted() {
            this.getCity();
            this.getAllProvince();
            this.getCityByProvince('上海');
        },
        computed: {
            columns() {
                return [{
                        values: this.provinceArrray,
                        className: 'provinceColumn'
                    },
                    {
                        values: this.cityArray,
                        className: 'cityColumn',
                        defaultIndex: 0 //默认选择哪一项子选项
                    }
                ]
            }
        },
        router
    }
</script>

<style>
    /* v-enter [这是一个时间点] 进入之前,元素初始状态,此时还没有开始进入 */
    /* v-leave-to [这是一个时间点] 是动画离开之后,离开的终止状态,此时动画已经结束 */
    .v-enter,
    .v-leave-to {
        opacity: 0;
        transform: translateX(1000px);
    }

    /* v-enter-active [入场动画的时间段] */
    /* v-leave-active [离场动画的时间段] */
    .v-enter-active,
    .v-leave-active {
        transition: all 0.4s ease;
    }

    main {
        height: 500px;
    }

    body {
        background-color: rgb(133, 216, 255);
    }
</style>