<template>
    <main id="loginAndRegister">
        <div style="margin-top:70px;">
            <transition mode="out-in">
                <component :is="loginOrRegister" :user="user" :data="data" ref="comView" v-on:finishCheck="finishCheck">
                </component>
            </transition>
            <div style="margin-top:8px;text-align:center;">
                <v-button :round="true" size="large" @click="doLogin();" :type="loginOrRegister=='login'?'info':'default'">{{loginOrRegister=='login'?'登录':'返回登录'}}
                </v-button><br>
                <v-button :round="true" size="large" @click="doRegister();" :type="loginOrRegister=='register'?'info':'default'">{{loginOrRegister=='register'?'注册':'注册新账号'}}
                </v-button>
            </div>
        </div>

        <!-- 提示未通过滑动验证 -->
        <popup v-model="showPop" round :style="{ height: '80px',width:'150px' }">
            <div style="text-align:center;margin-top:35px;">
                {{popMsg}}
            </div>
        </popup>
    </main>
</template>

<script>
    import login from '../../components/mobile/Login.vue';
    import register from '../../components/mobile/Register.vue';
    import {
        CellGroup,
        Field,
        Button,
        Popup
    } from 'vant';

    export default {
        name: 'loginAndRegister',
        components: {
            CellGroup,
            Field,
            vButton: Button,
            login,
            register,
            Popup
        },
        data() {
            return {
                loginOrRegister: 'login',
                user: {
                    name: '',
                    email: '',
                    password: '',
                    created: ''
                },
                data: {
                    confirmPassword: ''
                },
                goCheck: false, //是否通过滑动验证,
                showPop: false,
                popMsg: ''
            }
        },
        methods: {
            doLogin() {
                if (this.loginOrRegister == 'login') {
                    this.$refs.comView.passwordError = '';
                    this.$refs.comView.emailError = '';
                    this.axios({
                        method: 'post',
                        url: "/mobile/user/doLogin.action",
                        data: this.user
                    }).then((response) => {
                        var msg = "登录成功";
                        if (response.data == 1) {
                            this.$router.push("/user")
                        }
                        if (response.data == 2) { //密码错误
                            this.$refs.comView.passwordError = '密码有误';
                        }
                        if (response.data == 3) { //邮箱不存在
                            this.$refs.comView.emailError = '邮箱有误';
                        }
                        if (response.data == 4) { //邮箱格式有误   
                            this.$refs.comView.passwordError = '邮箱有误';
                        }
                        if (response.data == 5) { //该邮箱已被注册
                            this.$refs.comView.passwordError = '邮箱有误';
                        }
                        if (response.data == 6) { //遇到未知错误,请稍后重试
                            this.$refs.comView.passwordError = '未知错误,稍后重试';
                        }
                    }).catch((error) => {
                        console.log(error);
                    });
                } else {
                    this.clearUser();
                    this.loginOrRegister = 'login';
                }
            },
            finishCheck() {
                this.goCheck = true;
            },
            doRegister() { //注册服务
                if (this.loginOrRegister == 'register') {
                    this.$refs.comView.emailError = '';
                    this.$refs.comView.passwordError = '';
                    this.$refs.comView.confirmPasswordError = '';
                    var email = this.user.email;
                    var name = this.user.name;
                    var password = this.user.password;
                    var confirmPassword = this.data.confirmPassword;
                    if (!(/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/).test(email)) {
                        this.$refs.comView.emailError = '邮箱有误';
                    } else if (password == "") {
                        this.$refs.comView.passwordError = '请输入密码';
                    } else if (password != confirmPassword) {
                        this.$refs.comView.confirmPasswordError = '密码不一致';
                    } else if (!this.goCheck) {
                        this.popMsg = "请通过验证";
                        this.showPop = true;
                    } else { //前端检验通过
                        this.axios({
                            url: '/mobile/user/doRegist.action',
                            method: 'post',
                            data: this.user
                        }).then((response) => {
                            if (response.data == 1) { //注册成功
                                this.clearUser();
                                this.popMsg = "注册成功";
                                this.showPop = true;
                                this.loginOrRegister = "login";
                            }
                            if (response.data == 2) {
                                this.$refs.comView.emailError = '邮箱有误';
                            }
                            if (response.data == 3) {
                                msg = "密码不能为空";
                            }
                            if (response.data == 4) {
                                this.$refs.comView.emailError = '邮箱有误';
                            }
                            if (response.data == 5) {
                                this.$refs.comView.emailError = '邮箱已被注册';
                            }
                            if (response.data == 6) {
                                this.$refs.comView.emailError = '未知错误,稍后重试';
                            }
                        }).catch((error) => {
                            console.log(error);
                        });
                    }
                } else {
                    this.clearUser();
                    this.loginOrRegister = 'register';
                }

            },
            clearUser() { //清空当前用户信息
                this.user.email = '';
                this.user.password = '';
                this.user.name = '';
                this.user.created = '';
                this.userName = '';
                this.data.confirmPassword = '';
                if (this.loginOrRegister = 'login') {
                    this.$refs.passwordError = '';
                    this.$refs.emailError = '';
                }
                if (this.loginOrRegister = 'register') {
                    this.$refs.passwordError = '';
                    this.$refs.emailError = '';
                    this.$refs.confirmPasswordError = '';
                    this.$refs.nameError = '';
                }
                if (this.$route.path == "/user") {
                    this.$refs.routerView.getSubscription();
                    this.$refs.routerView.getUser();
                }
            },
        }
    }
</script>

