/*
路由器对象模块
*/
import Vue from 'vue';
import VueRouter from 'vue-router';

import weather from '../../views/mobile/Weather.vue';
import user from '../../views/mobile/User.vue';
import about from '../../views/mobile/About.vue';
import home from '../../views/mobile/Home.vue';
import loginRegister from '../../views/mobile/LoginRegister.vue';

//声明使用插件
Vue.use(VueRouter);


let router = new VueRouter({
    routes: [{
        path: '/',
        redirect: '/home'
    }, {
        path: '/weather',
        component: weather
    }, {
        path: '/user',
        component: user
    }, {
        path: '/about',
        component: about
    }, {
        path: '/home',
        component: home
    }, {
        path: '/login',
        component: loginRegister
    }]
});

//路由跳转后，页面回到顶部
router.afterEach(() => {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
});

export default router