<template>
    <main id="user">
        <div class="row" style="background-color:oldlace;margin-top:65px;">
            <div class="col-3">
                <img style="height:100px;" src="../../assets/images/common/LOGO_12_透明背景.png">
            </div>
            <div class="col-7" style="font-size:15px;margin-top:16px;">
                <div v-if="user.id!=''&&user.id!=null">
                    <div>姓名: {{user.name}}</div>
                    <div>邮箱: {{user.email}}</div>
                    <div>会员剩余: {{vip_time}}
                        <icon name="question-o" @click="showPop = true" size="10px" />
                    </div>
                </div>
                <div v-if="user.id==''||user.id==null"
                    style="text-align:center;font-size:18px;margin-top:20px;width:100%" @click="toLoginRegister">
                    快速登录
                </div>
            </div>

            <div class="col-1" style="position:flow;padding-top:30px;font-size:30px;text-align:left;">
                <icon name="arrow" size="35px" @click="toLoginRegister" />
            </div>
        </div>
        <div style="background-color:lemonchiffon;margin-top:25px;padding-bottom:2px;">
            <span style="text-align:center;font-size:20px;margin-left:8px;">
                <icon name="notes-o" style="margin-top:20px;" />
                订阅信息
                <button style="margin-left:10px;margin-bottom:6px;" class="btn btn-success glyphicon glyphicon-plus"
                    @click="addSubscription" />
            </span>
            <hr>
            <div v-if="subscriptionList.length==0" style="text-align:center;margin-bottom:15px;">
                <span style="font-size:20px;" @click="toLoginRegister"><b>暂无订阅信息</b></span>
            </div>
            <div v-if="user.id==''||user.id==null" style="text-align:center;margin-bottom:15px;">
                <span style="font-size:20px;" @click="toLoginRegister"><b>登录即可开始订阅管理</b></span>
            </div>
            <div class="row" style="margin-bottom:18px;margin-top:8px;" v-for="subscription in subscriptionList"
                :key="subscription.id">
                <div class="col-6" style="padding-left:25px;padding-top:3px;">
                    <span style="font-size:15px;">{{subscription.provinceZh}} - {{subscription.cityZh}}</span>
                </div>
                <div class="col-6">
                    <button class="btn btn-info glyphicon glyphicon-map-marker"
                        @click="$emit('setLocation',subscription.cityZh,subscription.provinceZh)">定位</button>
                    <button class="btn btn-danger glyphicon glyphicon-remove"
                        @click="deleteSubscription(subscription.id)">移除</button>
                </div>
            </div>
        </div>

        <!-- 会员续费通知 -->
        <popup v-model="showPop" round :style="{ height: '150px',width:'250px' }">
            <div style="text-align:center">
                <br>
                该App暂不支持会员续费功能,<br>如有需求可以通过PC端访问地址:<br>http://wzh.natapp1.cc/#/pay<br>进行会员开通续费服务,感谢谅解
            </div>
        </popup>
    </main>
</template>

<script>
    import dateUtil from '../../assets/js/date.js';

    import {
        List,
        Icon,
        Popup,
        Dialog
    } from 'vant';

    export default {
        name: 'user',
        components: {
            List,
            Icon,
            Popup,
            Dialog
        },
        data() {
            return {
                user: {
                    id: '',
                    name: '请登录',
                    email: '请登录',
                    vip_start: '请登录',
                    vip_stop: '请登录'
                },
                vip_time: '请登录',
                subscription: {
                    id: '',
                    uId: '',
                    provinceZh: '',
                    cityId: '',
                    cityZh: '',
                    ifEmail: ''
                },
                subscriptionList: [],
                ifModify: false,
                modifyOrSave: '修改个人信息',
                showPop: false,
            }
        },
        methods: {
            toLoginRegister() { //前往登录注册页,已登录则提示注销
                if (this.user.id != "请登录" && this.user.email != "请登录") {
                    Dialog.confirm({
                        title: '注销',
                        message: '退出当前账号?'
                    }).then(() => {
                        this.logout();
                    }).catch(() => {
                        // on cancel
                    });
                } else {
                    this.$router.push('/login');
                }
            },
            logout() { //注销服务
                this.axios({
                    url: "/mobile/user/logout.action",
                    method: "get"
                }).then((response) => {
                    if (response.data == 1) {
                        this.clearUser();
                    } else { //服务器错误

                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            clearUser() {
                this.getSubscription();
                this.getUser();
            },
            getUser() {
                this.axios({
                    url: '/mobile/user/getUserMessage.action',
                    method: 'get'
                }).then((response) => {
                    if (response.data != "" && response.data != null) {
                        this.user = response.data;
                        var vip_stop = response.data.vip_stop;
                        if (vip_stop == null) {
                            this.vip_time = "未开通或已到期";
                        } else {
                            var vipTime = dateUtil.getTimeSpace((new Date()).getTime(), vip_stop);
                            if (vipTime[0] > 0) {
                                this.vip_time = "剩余" + vipTime[0].toFixed(0) + "天";
                            } else {
                                this.vip_time = "未开通或已到期";
                            }
                        }
                    } else {
                        this.user.id = '';
                        this.user.name = '请登录';
                        this.user.email = '请登录';
                        this.user.vip_stop = '请登录';
                        this.vip_time = '请登录';
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            modifyUser() {
                if (this.user.email == "请登录") { //是否登录判断
                    this.$emit("showAlert", true, "未登录");
                } else {
                    if (this.ifModify == true) {
                        if (!(/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/).test(this.user
                                .email)) { //前端判断邮箱格式
                            this.$emit("showAlert", true, "请填写正确邮箱");
                        } else {
                            this.axios({
                                url: '/mobile/user/updateEmailAndName.action',
                                method: 'post',
                                data: this.user
                            }).then((response) => {
                                if (response.data == 1) { //修改成功
                                    this.modifyOrSave = "修改个人信息";
                                    this.ifModify = false;
                                    this.$emit("showAlert", true, "修改成功");
                                    this.$emit("getUser");
                                    this.getUser();
                                } else if (response.data == -1) { //未登录
                                    this.$emit("showAlert", true, "未登录");
                                } else if (response.data == 0) { //邮箱格式错误
                                    this.$emit("showAlert", true, "请填写正确邮箱");
                                } else if (response.data == 2) { //服务器异常
                                    this.$emit("showAlert", true, "服务器无响应,请稍后重试");
                                }
                            }).catch((error) => {
                                console.log(error);
                            });
                        }
                    } else {
                        this.ifModify = true;
                        this.modifyOrSave = "保存";
                    }
                }
            },
            getSubscription() {
                this.axios({
                    url: '/mobile/user/getSubscription.action',
                    method: 'get'
                }).then((response) => {
                    if (response.data == null) {
                        this.$emit("showAlert", true, "请登录");
                    } else {
                        this.subscriptionList = response.data;
                    }
                }).catch((error) => {
                    console.log(error);
                });
            },
            addSubscription() {
                Dialog.confirm({
                    title: '添加订阅',
                    message: '添加当前选中的地区?'
                }).then(() => {
                    // on confirm
                    this.axios({
                        url: '/mobile/user/addSubscription.action',
                        method: 'get'
                    }).then((response) => {
                        if (response.data == 1) { //订阅成功
                            this.getSubscription();
                        } else if (response.data == 0) { //未登录
                            Dialog.alert({
                                message: '请登录再重试'
                            }).then(() => {
                                // on close
                            });
                        } else if (response.data == -1) { //重复订阅
                            Dialog.alert({
                                message: '该地区已订阅'
                            }).then(() => {
                                // on close
                            });
                        } else if (response.data == 2) { //非会员订阅失败
                            Dialog.alert({
                                message: '订阅数量达到上限,开通会员可提高上限'
                            }).then(() => {
                                // on close
                            });
                        } else if (response.data == 3) { //会员订阅失败
                            Dialog.alert({
                                message: '抱歉!订阅失败,当前订阅个数已达到上限'
                            }).then(() => {
                                // on close
                            });
                        }
                    });
                }).catch(() => {
                    // on cancel
                });
            },
            deleteSubscription(id) {
                Dialog.confirm({
                    title: '移除订阅',
                    message: '取消订阅该城市?'
                }).then(() => {
                    this.axios({
                        url: '/mobile/user/deleteSubscription.action',
                        method: 'get',
                        params: {
                            id
                        }
                    }).then((response) => {
                        if (response.data == 1) { //移除成功
                            this.getSubscription();
                        } else if (response.data == -1) { //移除失败,登录信息过期
                            Dialog.alert({
                                message: '登录信息过期'
                            }).then(() => {
                                // on close
                            });
                        } else if (response.data == 0) { //移除失败,服务器异常
                            Dialog.alert({
                                message: '服务器异常'
                            }).then(() => {
                                // on close
                            });
                        }
                    }).catch((error) => {
                        console.log(error);
                    });
                }).catch(() => {
                    // on cancel
                });
            }
        },
        mounted() {
            this.getUser();
            this.getSubscription();
        },
        filters: {
            formatDate(value) {
                if (value == "请先登录") {
                    return null;
                }
                let date = new Date(value);
                let y = date.getFullYear();
                let MM = date.getMonth() + 1;
                MM = MM < 10 ? ('0' + MM) : MM;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                let h = date.getHours();
                h = h < 10 ? ('0' + h) : h;
                let m = date.getMinutes();
                m = m < 10 ? ('0' + m) : m;
                let s = date.getSeconds();
                s = s < 10 ? ('0' + s) : s;
                return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
            }
        },
    }
</script>

<style scoped>
    main {
        height: 600px;
    }
</style>