<template>
    <div id="demo">
        <div id="slider">
            <div id="slider_bg"></div>
            <span id="label">>></span>
            <span id="labelTip">拖动滑块验证</span>
        </div>
    </div>
</template>

<script>
    import '../../assets/js/jquery.slideunlock.js';

    export default {
        name: 'sliderCheck',
        methods: {
            sliderUnlock() {
                var _this = this;
                var slider = new SliderUnlock("#slider", {
                    successLabelTip: "完成验证!"
                }, function () {
                    _this.$emit("finishCheck");
                });
                slider.init();
            }
        },
        mounted() {
            this.sliderUnlock();
        }
    }
</script>

<style scoped>
    html,
    body,
    h1 {
        margin: 0;
        padding: 0;
    }

    body {
        background-color: #393939;
        color: #d5d4ff;
        overflow: hidden
    }


    #slider {
        margin: auto;
        width: 300px;
        height: 40px;
        position: relative;
        border-radius: 2px;
        background-color: #dae2d0;
        overflow: hidden;
        text-align: center;
        user-select: none;
        -moz-user-select: none;
        -webkit-user-select: none;
    }

    #slider_bg {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        background-color: #7AC23C;
        z-index: 1;
    }

    #label {
        width: 46px;
        position: absolute;
        left: 0;
        top: 0;
        height: 38px;
        line-height: 38px;
        border: 1px solid #cccccc;
        background: #fff;
        z-index: 3;
        cursor: move;
        color: #ff9e77;
        font-size: 16px;
        font-weight: 900;
    }

    #labelTip {
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        font-size: 13px;
        font-family: 'Microsoft Yahei', serif;
        color: #787878;
        line-height: 38px;
        text-align: center;
        z-index: 2;
    }

    #sliderTitle {
        text-align: center;
    }

    /* #demo {
        width: 600px;
        margin: 150px auto;
        padding: 10px;
        border: 1px dashed #d5d4ff;
        border-radius: 10px;
        -moz-border-radius: 10px;
        -webkit-border-radius: 10px;
        text-align: left;
    } */
</style>